}
.number-keyboard .fadeIn-keyboard {
  -webkit-animation-name: fadeIn-keyboard;
  animation-name: fadeIn-keyboard;
}
.number-keyboard .slideOutDown-keyboard {
  -webkit-animation-name: slideOutDown-keyboard;
  animation-name: slideOutDown-keyboard;
}
.number-keyboard .slideInUp-keyboard {
  -webkit-animation-name: slideInUp-keyboard;
  animation-name: slideInUp-keyboard;
}
.number-keyboard .number-keyboard-content-wrapper {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: transparent;
  z-index: 2147483647;
}
.number-keyboard .number-keyboard-content-wrapper::after {
  position: absolute;
  left: 0;
  top: 0;
  display: block;
  width: 100%;
  border-top: 1px solid #e0e0e0;
  content: '';
}
.number-keyboard .number-keyboard-content-wrapper .close-keyboard {
  display: block;
  height: 30px;
  text-align: center;
  background: #fff;
  position: relative;
}
.number-keyboard .number-keyboard-content-wrapper .close-keyboard::after {
  position: absolute;
  left: 0;
  bottom: 0;
  display: block;
  width: 100%;
  border-bottom: 1px solid #e0e0e0;
  content: '';
}
.number-keyboard .number-keyboard-content-wrapper .close-keyboard .pic {
  display: inline-block;
  margin-top: 3px;
  width: 30px;
  height: 24px;
  background: url("data:image/png;base64,");
  background-size: 30px 24px;
}
.number-keyboard .number-keyboard-content-wrapper > ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: #fff;
  height: 60px;
  line-height: 60px;
  overflow: hidden;
}
.number-keyboard .number-keyboard-content-wrapper > ul > li {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  font-size: 28px;
  background: #fff;
  touch-callout: none;
  -webkit-touch-callout: none;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
  -webkit-user-select: none;
