
::-webkit-scrollbar {
  width: 0;
  height: 0;
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px::after {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
.tablayout {
  width: 100%;
  overflow-y: hidden;
  overflow-x: auto;
  position: relative;
}
.tablayout .tablayout-wrapper {
  height: 44px;
  z-index: 2147483644;
  position: relative;
}
.tablayout .tablayout-wrapper::after {
  position: absolute;
  left: 0;
  bottom: 0;
  display: block;
  width: 100%;
  border-bottom: 1px solid rgba(0,0,0,0.1);
  content: '';
}
.tablayout .tablayout-wrapper .tablayout-item {
  float: left;
  height: 44px;
  line-height: 44px;
  text-align: center;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
  -webkit-user-select: none;
  touch-callout: none;
  -webkit-touch-callout: none;
}
.tablayout .tablayout-wrapper .tablayout-item > a {
  display: block;
  width: 100%;
  height: 100%;
  font-size: 14px;
}
.tablayout .tablayout-wrapper .tablayout-item > a.active {
  font-size: 16px;
  color: #d74f43 !important;
  font-weight: 500;
}
.tablayout .tablayout-wrapper .tablayout-item > a:active {
  background: #f7f7f7;
}
.tablayout .indicator {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 2147483645;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  height: 2px;
  background: #d74f43;
}


