
.sharewx .wxjoylayer {
  background: url("data:image/png;base64,") no-repeat 100% 10px #333;
  background-size: 140px auto;
  width: 100%;
  height: 100%;
  opacity: 0.9;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  z-index: 2147483646;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.sharewx .wxjoylayer.fade-enter-active,
.sharewx .wxjoylayer.fade-leave-active {
  opacity: 0.9;
}
.sharewx .wxjoylayer.fade-enter,
.sharewx .wxjoylayer.fade-leave-active {
  opacity: 0;
}
.sharewx .wxjoylayer > span {
  padding: 90px 0 0 15px;
  font-size: 14px;
  line-height: 26px;
  color: #fff;
  display: block;
  overflow: hidden;
}


