
.toast .content {
  position: fixed;
  bottom: 50px;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  margin: 0 auto;
  font-size: 14px;
  min-width: 100px;
  height: 36px;
  line-height: 36px;
  text-align: center;
  padding: 0 14px;
  border-radius: 18px;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  background: rgba(0,0,0,0.7);
  color: #fff;
  z-index: 2147483647;
  -webkit-transition: all 0.6s;
  transition: all 0.6s;
}
.toast .content.fade-enter-active,
.toast .content.fade-leave-active {
  opacity: 1;
}
.toast .content.fade-enter,
.toast .content.fade-leave-active {
  opacity: 0;
}


