
.floatbutton {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  -webkit-box-shadow: 0 1px 2px #999;
          box-shadow: 0 1px 2px #999;
  position: relative;
  touch-callout: none;
  -webkit-touch-callout: none;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
  -webkit-user-select: none;
  z-index: 2147483646;
}
.floatbutton::after {
  position: absolute;
  display: block;
  content: "";
  left: 50%;
  top: 50%;
  width: 0;
  height: 0;
  background: rgba(255,255,255,0.1);
  border-radius: 50%;
  -webkit-transition: all 0.15s;
  transition: all 0.15s;
}
.floatbutton .floatbutton-icon {
  display: inline-block;
  width: 24px;
  height: 24px;
  background: url("data:image/png;base64,") no-repeat center;
  background-size: cover;
  margin: 12px;
}
.floatbutton:active::after {
  margin-top: -5px;
  margin-left: -5px;
  width: 10px;
  height: 10px;
  -webkit-transform: scale(5);
          transform: scale(5);
}


