
.icontoast .icontoast-wrapper {
  position: fixed;
  min-width: 110px;
  height: 90px;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin: auto;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  background: rgba(0,0,0,0.8);
  border-radius: 6px;
  -webkit-transition: all 0.6s;
  transition: all 0.6s;
  z-index: 2147483647;
}
.icontoast .icontoast-wrapper.fade-enter-active,
.icontoast .icontoast-wrapper.fade-leave-active {
  opacity: 1;
}
.icontoast .icontoast-wrapper.fade-enter,
.icontoast .icontoast-wrapper.fade-leave-active {
  opacity: 0;
}
.icontoast .icontoast-wrapper .icon {
  display: block;
  width: 38px;
  height: 38px;
  margin: 12px auto;
}
.icontoast .icontoast-wrapper .icon.succeed {
  background: url("data:image/png;base64,") no-repeat center;
  background-size: contain;
}
.icontoast .icontoast-wrapper .icon.failed {
  background: url("data:image/png;base64,") no-repeat center;
  background-size: contain;
}
.icontoast .icontoast-wrapper .text {
  display: block;
  width: 100%;
  padding: 0 10px;
  text-align: center;
  line-height: 14px;
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}


