
.switch {
  display: inline-block;
  width: 44px;
  height: 22px;
  position: relative;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
  -webkit-user-select: none;
  touch-callout: none;
  -webkit-touch-callout: none;
}
.switch .switch-wrapper > .switch-input {
  display: none;
}
.switch .switch-wrapper .switch-input + label {
  display: inline-block;
  width: 44px;
  height: 22px;
  position: relative;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.switch .switch-wrapper > .switch-input + label:after {
  content: '';
  display: block;
  position: absolute;
  left: 2px;
  top: 2px;
  width: 14px;
  height: 14px;
  background: #e2e2e2;
  border-radius: 50%;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  cursor: pointer;
}
.switch .switch-wrapper > .switch-input:checked + label.green {
  border-color: rgba(65,184,131,0.6);
}
.switch .switch-wrapper > .switch-input:checked + label.green:after {
  background: #41b883;
}
.switch .switch-wrapper > .switch-input:checked + label:after {
  left: calc(100% - 16px);
}
.switch .switch-wrapper > .switch-input:checked + label + div::before {
  display: inline-block;
}
.switch .switch-wrapper > .switch-input:checked + label + div::after {
  display: none;
}
.switch .switch-wrapper > .switch-input + label {
  border: 2px solid #e2e2e2;
  border-radius: 22px;
  z-index: 10;
}
.switch .switch-wrapper > .switch-tips {
  position: absolute;
  top: 0;
  left: 0;
  width: 44px;
  height: 22px;
}
.switch .switch-wrapper > .switch-tips::before {
  position: absolute;
  top: 0;
  left: 8px;
  display: none;
  color: rgba(65,184,131,0.6);
}
.switch .switch-wrapper > .switch-tips::after {
  position: absolute;
  top: 0;
  right: 8px;
  display: inline-block;
  color: #e2e2e2;
}
.switch .switch-wrapper > .switch-tips.default::before {
  line-height: 24px;
  font-size: 16px;
  content: '•';
}
.switch .switch-wrapper > .switch-tips.default::after {
  line-height: 23px;
  font-size: 12px;
  content: '×';
}
.switch .switch-wrapper > .switch-tips.on-off::before {
  line-height: 21px;
  font-size: 12px;
  content: 'on';
}
.switch .switch-wrapper > .switch-tips.on-off::after {
  line-height: 24px;
  font-size: 12px;
  content: 'off';
}
.switch .switch-wrapper > .switch-tips.open-close::before {
  line-height: 22px;
  font-size: 12px;
  content: '开';
}
.switch .switch-wrapper > .switch-tips.open-close::after {
  line-height: 22px;
  font-size: 12px;
  content: '关';
}


