
.badge {
  display: inline-block;
  color: #fff;
  min-width: 10px;
  min-height: 10px;
  background: #f74c31;
  border-radius: 5px;
}
.badge.badge-no-empty {
  font-size: 12px;
  text-align: center;
  padding: 2px 3px;
  border-radius: 8px;
}
.badge.badge-other {
  padding: 2px 5px;
}


