
::-webkit-scrollbar {
  width: 0;
  height: 0;
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px::after {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
.border-1px::before {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
.border-1px::before {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
.cell-box {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    position: relative;
    height: 48px;
    line-height: 48px;
}
.cell-box::after {
    position: absolute;
    left: 0;
    bottom: 0;
    display: block;
    width: 100%;
    border-bottom: 1px solid rgba(0,0,0,0.1);
    content: '';
}
.cell-box .cell-box-title {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding-left: 5px;
}
.cell-box .cell-box-des {
    width: 100px;
    -webkit-box-flex: 0;
        -ms-flex: 0 0 100px;
            flex: 0 0 100px;
}
.cell-box .cell-box-des > div {
    width: 71px;
    font-size: 12px;
    overflow: hidden;
    color: #999;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.cell-box .cell-box-des::after {
    content: "";
    position: absolute;
    right: 5px;
    top: 50%;
    margin-top: -7px;
    display: inline-block;
    width: 14px;
    height: 14px;
    background: url("data:image/png;base64,") no-repeat center;
    background-size: cover;
}
}


