
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  color: #2c3e50;
  padding: 10px;
}
.tab-wrapper {
  background: #fff;
}
h1 {
  margin-top: 10px;
  height: 30px;
}
button {
  margin: 5px 10px 10px 0;
}
.floatbutton-wrapper {
  position: fixed;
  bottom: 16px;
  right: 16px;
}
.floatbutton-wrapper1 {
  position: fixed;
  bottom: 80px;
  right: 16px;
}
.tab-wrapper {
  position: relative;
}
.tab-wrapper::before {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  content: "";
  width: 22px;
  height: 43px;
  z-index: 2147483645;
  background: -webkit-linear-gradient(left, white, rgba(255, 255, 255, 0));
}
.tab-wrapper::after {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  content: "";
  width: 22px;
  height: 43px;
  z-index: 2147483645;
  background: -webkit-linear-gradient(right, white, rgba(255, 255, 255, 0));
}



