
::-webkit-scrollbar {
  width: 0;
  height: 0;
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px::after {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
.border-1px::before {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
.border-1px::before {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
.cell-swipe {
  height: 48px;
  line-height: 48px;
  position: relative;
  background: #f7f7f7;
  overflow: hidden;
}
.cell-swipe .cell-swipe-content {
  display: block;
  background: #fff;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  position: relative;
}
.cell-swipe .cell-swipe-content::after {
  position: absolute;
  left: 0;
  bottom: 0;
  display: block;
  width: 100%;
  border-bottom: 1px solid rgba(0,0,0,0.1);
  content: '';
}
.cell-swipe .cell-swipe-options {
  position: absolute;
  top: 0;
  bottom: 0;
}
.cell-swipe .cell-swipe-options > span {
  color: #fff;
  padding: 0 16px;
  display: inline-block;
  height: 100%;
}
.cell-swipe .cell-swipe-options > span:nth-child(1) {
  background: #d3d3d3;
}
.cell-swipe .cell-swipe-options > span:nth-child(2) {
  background: #f00;
}


